import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import DatePicker from 'material-ui/DatePicker';
import axios from 'axios';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      controlledDate: null,
      message: "Pick date",
    };
  }

//DD.MM.YYYY HH:mm:ss


  handleChange = (event, date) => {
    this.setState({
      controlledDate: date,
      message: 'Sending...',
    });

    var self = this;

    let dateSending = date.getDate() + '.' + date.getMonth() + '.' + date.getFullYear() + ' ' 
      + date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds();
    axios.post('https://server-node-material-ui.herokuapp.com/', {
      date: dateSending
    })
    .then(function (response) {
      self.setState({
        message: response.data,
      });
    })
    .catch(function (error) {
      self.setState({
        message: error,
      });
    });

  };


  render() {
    return (
      <div className="App">
        <p className="App-intro">
          <MuiThemeProvider>
            <DatePicker 
            hintText="Date"
            value={this.state.controlledDate}
            onChange={this.handleChange}/>
          </MuiThemeProvider>
          <br/>
          <span>{this.state.message}</span>
        </p>
      </div>

    );
  }
}

export default App;



